<script>

import {branches} from "@/model/learn-sim";
import {header} from "@/utils/DataTableUtil";

const branchesRows = [
  ['飞雪', '连城', '天龙', '射雕', '白马', '鹿鼎'],
  ['笑傲', '书剑', '神雕', '侠客', '倚天', '碧血']
]

const attrs = [
    '攻击', '防御', '轻功', '拳掌', '御剑', '兵器', '指腿', '暗毒'
]

const learnType = ['修炼', '洗武', '加属性']
const bookType = [ '拳掌', '御剑', '兵器', '指腿', '暗毒', '内功', '轻功', '特殊' ]

const learnHeaders = [
  header('', '', '栏位', 'col', { align: 'start' }),
]

const bookHeaders = [
    header('', '', '等级', 'level', { align: 'start' }),
    header('', '', '名称', 'name', { sortable: false }),
    header('', '', '修炼武功', 'skill', { sortable: false })
]

export default {
  name: "LearnSimulator",
  components: {

  },
  data() {
    return {
      config: {
        branches,
        attrs,
        learnType,
        bookType
      }
    }
  },
  computed: {
    branches() {
      const res = branchesRows.map((row, i) => {
        return {
          index: i,
          cols: row.map((name, j) => {
            return {
              index: j,
              name: name,
              list: branches[name]
            }
          })
        }
      })
      return res;
    },
    learnHeaders() {
      return learnHeaders;
    },
    bookHeaders() {
      return bookHeaders;
    }
  },
}
</script>


